.form__col { float: left; padding: 0 15px; width: 50%; }

@media only screen and (max-width: 599px) { .form__col { width: 100%; } }

@media only screen and (max-width: 599px) { .form__col:not(:last-child) { margin-bottom: 30px; } }

.form__button-wrapper { margin-top: 28px; }

.form__title { margin-bottom: 38px; }

.form__parking { margin-top: 32px; font-size: 14px; font-weight: 400; text-align: center; }

.form__parking .icon { margin: 0 7px 0 0; font-size: 20px; color: #76c93f; vertical-align: top; display: inline-block; }

.advantages { margin: 66px auto 28px; }

@media only screen and (max-width: 767px) { .advantages { margin-top: 32px; } }

.advantages__title { margin-bottom: 74px; }

@media only screen and (max-width: 767px) { .advantages__title { margin-bottom: 32px; } }

.advantages__items { display: flex; justify-content: center; flex-wrap: wrap; margin: -12px; }

.item { flex: 0 0 20%; width: 20%; text-align: center; padding: 16px 12px 0; }

@media only screen and (max-width: 767px) { .item { min-width: 144px; padding: 24px 10px 0; } }

.item__image { margin-bottom: 16px; }

.item_smaller .item__image { margin-bottom: 11px; }

.item__image .icon { font-size: 64px; display: inline-block; }

.item__text { font-weight: 700; font-style: italic; }

.clients__logos { display: flex; align-items: center; justify-content: center; flex-wrap: wrap; margin: 42px auto 72px; }

@media only screen and (max-width: 767px) { .clients__logos { margin: 32px auto; } }

.clients__logo { display: inline-block; vertical-align: middle; filter: grayscale(100%) opacity(0.7); transition: filter 0.4s; padding: 0 24px; }

.clients__logo:hover { filter: grayscale(0) opacity(1); }

.clients__logo img { max-width: 100%; }

.slick-track { display: flex; align-items: center; }

.slick-slide div { text-align: center; font-size: 0; }

.slick-slide div:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.discount { height: 200px; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; position: relative; text-align: center; padding: 35px 0; }

.discount:after { position: absolute; top: 0; left: 0; width: 100%; height: 100%; content: ''; background-color: rgba(248, 251, 241, 0.92); z-index: 1; }

.discount__title { font-size: 20px; line-height: 1.4; font-weight: 400; position: relative; z-index: 2; margin-bottom: 30px; }

@media only screen and (max-width: 767px) { .discount__title { font-size: 16px; } }

.discount .btn-discount { position: relative; z-index: 2; }

.btn-discount { display: inline-block; text-align: center; line-height: 1.75; border: 2px solid #8ade6d; min-width: 390px; padding: 12px 10px; background-color: #fff; font-size: 16px; font-family: "PTAstraSans", "Trebuchet MS", "Helvetica Neue", Arial, Helvetica, sans-serif; font-weight: 700; word-spacing: 0.07em; }

@media only screen and (max-width: 767px) { .btn-discount { min-width: inherit; line-height: 1; } }

.awards { margin: 86px auto 72px; }

@media only screen and (max-width: 767px) { .awards { margin: 32px auto; } }

.awards__title { margin-bottom: 60px; }

@media only screen and (max-width: 767px) { .awards__title { margin-bottom: 16px; } }

.awards__items { display: flex; justify-content: center; flex-wrap: wrap; margin: -12px; }

.dishes { margin: 40px auto 80px; }

@media only screen and (max-width: 767px) { .dishes { margin-bottom: 32px; } }

.dishes__title { margin-bottom: 31px; }

.footer { text-align: center; padding-top: 38px; padding-bottom: 6px; }

.footer__copyright { margin-top: 16px; }

.halls { margin: 107px auto 21px; }

@media only screen and (max-width: 767px) { .halls { margin: 32px auto; } }

.halls__title { margin-bottom: 47px; }

.halls__halls { display: flex; justify-content: center; flex-wrap: wrap; margin: 0 -15px; }

.hall { flex: 0 0 50%; width: 50%; padding: 0 15px 46px; text-align: center; }

@media only screen and (max-width: 767px) { .hall { flex: 0 0 100%; width: 100%; padding-bottom: 32px; } }

.hall__image { display: block; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; padding-bottom: 314px; margin-bottom: 26px; }

@media only screen and (max-width: 767px) { .hall__image { margin-bottom: 12px; } }

.hall__title { font-size: 20px; font-weight: 700; line-height: 1.05; text-transform: uppercase; margin-bottom: 8px; }

@media only screen and (max-width: 767px) { .hall__title { font-size: 18px; } }

.hall__props { font-size: 18px; line-height: 1.17; margin-bottom: 9px; }

@media only screen and (max-width: 767px) { .hall__props { font-size: 16px; } }

.hall__square { display: inline-block; margin-right: 5px; }

@media only screen and (max-width: 1023px) { .hall__square { width: 100%; } }

.hall__seats { display: inline-block; }

@media only screen and (max-width: 767px) { .hall__seats { width: 100%; } }

.hall__descr { max-width: 480px; margin: 0 auto 7px; }

.hall__link { font-size: 16px; font-weight: 700; color: #559f3c; border-bottom: 1px solid #559f3c; display: inline-block; margin-bottom: 12px; transition: border-bottom-color 0.4s; }

.hall__link:hover { border-bottom-color: #000; }

.slider { z-index: 1; white-space: nowrap; opacity: 0; visibility: hidden; }

.slider.slick-initialized { opacity: 1; visibility: visible; }

.slider .slick-track { white-space: nowrap; }

.slider .slick-slide { display: inline-block; }

.slick-initialize .slider .slick-slide { display: block; }

.slider:hover .slider__slide:not(:hover):after { opacity: 1; visibility: visible; }

.slider__slide { position: relative; display: inline-block; }

.slider__slide:after { position: absolute; top: 0; left: 0; width: 100%; height: 100%; content: ''; z-index: 1; background-color: rgba(0, 0, 0, 0.5); opacity: 0; visibility: hidden; transition: opacity 0.4s, visibility 0.4s; }

.slider__image { padding-bottom: 212px; width: 305px; display: block; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

.btn-slider { height: 40px; width: 40px; border-radius: 50%; font-size: 0; text-align: center; vertical-align: middle; background-color: rgba(255, 255, 255, 0.5); z-index: 50; }

.btn-slider:hover { background-color: rgba(255, 255, 255, 0.9); }

.btn-slider .icon { font-size: 20px; vertical-align: middle; color: rgba(0, 0, 0, 0.7); }

.btn-slider { position: absolute; top: 50%; transform: translateY(-50%); }

.btn-slider_prev { left: 40px; }

.btn-slider_next { right: 40px; }

#map { height: 400px; width: 100%; }

.contacts { margin: 24px 0 0; }

.contacts__title { margin-bottom: 20px; }

.map-wrapper { position: relative; border-bottom: 1px solid #cbcbcb; border-top: 1px solid #cbcbcb; }

.reserve { position: absolute; right: calc(50% - 585px); top: 50%; transform: translateY(-50%); background-color: #fff; z-index: 5; padding: 38px; }

@media only screen and (max-width: 1259px) { .reserve { right: calc(50% - 485px); } }

@media only screen and (max-width: 1023px) { .reserve { position: static; transform: none; } }

.reserve__adress, .reserve__phone, .reserve__time { padding-left: 22px; position: relative; letter-spacing: 0.04em; word-spacing: -0.05em; }

.reserve__adress { padding-bottom: 15px; }

.reserve__last { padding-bottom: 22px; }

.reserve__icon { position: absolute; left: 0; top: 2px; }

.reserve__icon .icon { color: #999999; }

.reserve__time .reserve__icon .icon { font-size: 17px; }

.reserve__form { display: flex; align-items: center; margin-top: 30px; flex-wrap: wrap; }

.reserve__button { display: inline-block; width: 264px; padding: 15px; border: 2px solid #cccccc; line-height: 1.5; font-size: 14px; font-family: "PTAstraSans", "Trebuchet MS", "Helvetica Neue", Arial, Helvetica, sans-serif; font-weight: 700; text-align: center; transition: border-color 0.4s, color 0.4s, background-color 0.4s; }

@media only screen and (max-width: 1259px) { .reserve__button { width: 200px; } }

@media only screen and (max-width: 767px) { .reserve__button { width: 100%; padding: 10px; } }

.reserve__button:first-child { margin-right: 24px; }

@media only screen and (max-width: 767px) { .reserve__button:first-child { margin-right: 0; margin-bottom: 16px; } }

.reserve__button:hover { border: 2px solid #8ade6d; }

.reserve__button_highlighted { border: 2px solid #8ade6d; }

.reserve__button_highlighted:hover { color: #fff; border-color: #8ade6d; background-color: #8ade6d; }

[data-modal] { position: fixed; z-index: -1; top: 0; left: 0; right: 0; bottom: 0; overflow: auto; padding: 20px; opacity: 0; visibility: hidden; text-align: center; font-size: 0; white-space: nowrap; transform: translate3d(0, 0, 0); transition: opacity 0.3s, visibility 0.3s, z-index 0.3s; background-color: rgba(0, 0, 0, 0.5); }

[data-modal]:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

[data-modal].is-open { opacity: 1; visibility: visible; z-index: 1000; }

[data-modal-container] { position: relative; padding: 45px 35px 48px; z-index: 20; border-radius: 4px; width: 100%; max-width: 570px; white-space: normal; text-align: left; vertical-align: middle; overflow: hidden; background-color: #fff; box-shadow: 0 0 25px rgba(43, 45, 66, 0.25); display: inline-block; }

[data-modal-close] { position: absolute; top: 0; right: 0; padding: 20px; background-color: transparent; font-size: 0; }

.modal__close .icon { color: #ccc; font-size: 14px; }
