.halls {
    margin: 107px auto 21px;
    @include sm {
        margin: 32px auto; }
    &__title {
        margin-bottom: 47px; }
    &__halls {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        margin: 0 -15px; } }
.hall {
    flex: 0 0 50%;
    width: 50%;
    padding: 0 15px 46px;
    text-align: center;
    @include sm {
        flex: 0 0 100%;
        width: 100%;
        padding-bottom: 32px; }
    &__image {
        display: block;
        @include cover;
        padding-bottom: 314px;
        margin-bottom: 26px;
        @include sm {
            margin-bottom: 12px; } }
    &__title {
        font-size: 20px;
        font-weight: 700;
        line-height: 1.05;
        text-transform: uppercase;
        margin-bottom: 8px;
        @include sm {
            font-size: 18px; } }
    &__props {
        font-size: 18px;
        line-height: 1.17;
        margin-bottom: 9px;
        @include sm {
            font-size: 16px; } }
    &__square {
        display: inline-block;
        margin-right: 5px;
        @include md {
            width: 100%; } }
    &__seats {
        display: inline-block;
        @include sm {
            width: 100%; } }
    &__descr {
        max-width: 480px;
        margin: 0 auto 7px; }
    &__link {
        font-size: 16px;
        font-weight: 700;
        color: $darkGreen;
        border-bottom: 1px solid $darkGreen;
        display: inline-block;
        margin-bottom: 12px;
        transition: border-bottom-color $tr;
        &:hover {
            border-bottom-color: $black; } } }

